import { NextRequest, NextResponse } from 'next/server';
import { db } from '@/lib/firebase';
import { collection, addDoc, getDocs, query, orderBy, limit } from 'firebase/firestore';
import { OrderItem, CustomerInfo } from '@/types/product';

interface OrderData {
  customer: CustomerInfo;
  items: OrderItem[];
  subtotal?: number;
  shippingCost?: number;
  total?: number;
  paymentMethod: 'cash' | 'credit_card' | 'bank_transfer';
  shippingMethod?: 'standard' | 'express' | 'pickup';
  createdAt: string;
}

// Mail gÃ¶nderme fonksiyonu
async function sendOrderEmails(order: OrderData, orderNumber: string) {
  try {
    const adminEmail = process.env.ADMIN_EMAIL || 'info@celenkdiyari.com';
    const customerEmail = order.customer?.email;
    
    if (!customerEmail) {
      console.warn('âš ï¸ Customer email not found, skipping email send');
      return;
    }

    // SipariÅŸ Ã¶zeti oluÅŸtur
    const itemsList = order.items.map((item: OrderItem) => 
      `- ${item.productName}${item.variantName ? ` (${item.variantName})` : ''} x${item.quantity} = ${(item.price * item.quantity).toFixed(2)} â‚º`
    ).join('\n');

    const addressText = order.customer.address 
      ? `${order.customer.address.street}, ${order.customer.address.district}, ${order.customer.address.city}${order.customer.address.postalCode ? ` - ${order.customer.address.postalCode}` : ''}`
      : 'Adres bilgisi yok';

    const paymentMethodText = 
      order.paymentMethod === 'cash' ? 'KapÄ±da Ã–deme' :
      order.paymentMethod === 'credit_card' ? 'Kredi KartÄ±' :
      'Havale/EFT';

    // KullanÄ±cÄ±ya gÃ¶nderilecek mail
    // EmailJS'deki mevcut template ID: template_t6bsxpr (Customer Order Template)
    const customerTemplateId = process.env.EMAILJS_CUSTOMER_TEMPLATE_ID || process.env.EMAILJS_TEMPLATE_ID || 'template_t6bsxpr';
    if (!customerTemplateId) {
      console.warn('âš ï¸ Customer email template ID not found');
      return; // Template ID yoksa mail gÃ¶nderme
    }
    
    const customerEmailData = {
      to: customerEmail,
      subject: `SipariÅŸiniz AlÄ±ndÄ± - ${orderNumber}`,
      templateId: customerTemplateId,
      templateParams: {
        order_number: orderNumber,
        customer_name: `${order.customer.firstName} ${order.customer.lastName}`,
        items: itemsList,
        subtotal: order.subtotal?.toFixed(2) || '0.00',
        shipping_cost: order.shippingCost?.toFixed(2) || '0.00',
        total: order.total?.toFixed(2) || '0.00',
        payment_method: paymentMethodText,
        address: addressText,
        phone: order.customer.phone || '',
        notes: order.customer.notes || 'Yok',
        order_date: new Date(order.createdAt).toLocaleDateString('tr-TR', {
          year: 'numeric',
          month: 'long',
          day: 'numeric',
          hour: '2-digit',
          minute: '2-digit'
        })
      }
    };

    // Admin'e gÃ¶nderilecek mail
    // EmailJS'deki mevcut template ID: template_zel5ngx (Admin Order Template)
    const adminTemplateId = process.env.EMAILJS_ADMIN_TEMPLATE_ID || process.env.EMAILJS_TEMPLATE_ID || 'template_zel5ngx';
    if (!adminTemplateId) {
      console.warn('âš ï¸ Admin email template ID not found');
      // Admin template yoksa sadece customer maili gÃ¶nder
    }
    
    const adminEmailData = {
      to: adminEmail,
      subject: `Yeni SipariÅŸ - ${orderNumber}`,
      templateId: adminTemplateId,
      templateParams: {
        order_number: orderNumber,
        customer_name: `${order.customer.firstName} ${order.customer.lastName}`,
        customer_email: customerEmail,
        customer_phone: order.customer.phone || '',
        items: itemsList,
        subtotal: order.subtotal?.toFixed(2) || '0.00',
        shipping_cost: order.shippingCost?.toFixed(2) || '0.00',
        total: order.total?.toFixed(2) || '0.00',
        payment_method: paymentMethodText,
        address: addressText,
        notes: order.customer.notes || 'Yok',
        order_date: new Date(order.createdAt).toLocaleDateString('tr-TR', {
          year: 'numeric',
          month: 'long',
          day: 'numeric',
          hour: '2-digit',
          minute: '2-digit'
        })
      }
    };

    // Email gÃ¶nderme fonksiyonunu direkt import edip kullanmak yerine
    // internal API route'u Ã§aÄŸÄ±rÄ±yoruz (Vercel'de Ã§alÄ±ÅŸmasÄ± iÃ§in)
    // Vercel'de internal API call iÃ§in doÄŸru URL kullanÄ±yoruz
    const getBaseUrl = () => {
      // Vercel production'da
      if (process.env.VERCEL_URL) {
        return `https://${process.env.VERCEL_URL}`;
      }
      // Local development
      return process.env.NEXT_PUBLIC_BASE_URL || 'http://localhost:3000';
    };

    const baseUrl = getBaseUrl();
    
    // Her iki maili de paralel gÃ¶nder
    const emailPromises = [];
    
    // Customer email
    if (customerTemplateId) {
      emailPromises.push(
        fetch(`${baseUrl}/api/email`, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(customerEmailData)
        }).then(async (res) => {
          const result = await res.json();
          if (res.ok) {
            console.log('âœ… Customer email sent successfully');
          } else {
            console.error('âŒ Failed to send customer email:', result);
          }
          return { ok: res.ok, result };
        }).catch(err => {
          console.error('âŒ Customer email fetch error:', err);
          return { ok: false, error: err.message };
        })
      );
    }
    
    // Admin email
    if (adminTemplateId) {
      emailPromises.push(
        fetch(`${baseUrl}/api/email`, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(adminEmailData)
        }).then(async (res) => {
          const result = await res.json();
          if (res.ok) {
            console.log('âœ… Admin email sent successfully');
          } else {
            console.error('âŒ Failed to send admin email:', result);
          }
          return { ok: res.ok, result };
        }).catch(err => {
          console.error('âŒ Admin email fetch error:', err);
          return { ok: false, error: err.message };
        })
      );
    }
    
    // TÃ¼m mailleri paralel gÃ¶nder
    await Promise.allSettled(emailPromises);

  } catch (error) {
    console.error('âŒ Error in sendOrderEmails:', error);
    // Hata olsa bile devam et, sipariÅŸ zaten oluÅŸturuldu
  }
}

export async function POST(request: NextRequest) {
  try {
    console.log('ðŸ›’ Creating new order...');
    
    const orderData = await request.json();
    console.log('ðŸ“ Order data:', orderData);
    
    // Validate required fields
    if (!orderData.customer || !orderData.items || !orderData.items.length) {
      console.log('âŒ Validation failed - missing required fields');
      return NextResponse.json({
        error: 'Missing required fields: customer, items',
        received: orderData
      }, { status: 400 });
    }
    
    // Generate order number
    const orderNumber = `ORD-${Date.now()}-${Math.random().toString(36).substr(2, 4).toUpperCase()}`;
    
    const order = {
      ...orderData,
      orderNumber,
      status: 'pending',
      paymentStatus: 'pending',
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString()
    };
    
    console.log('âœ… Validation passed, creating order:', orderNumber);
    
    const docRef = await addDoc(collection(db, 'orders'), order);
    
    console.log('âœ… Order created successfully in Firebase with ID:', docRef.id);
    
    // Mail gÃ¶nderme iÅŸlemi (async olarak Ã§alÄ±ÅŸÄ±r, hata olsa bile sipariÅŸ oluÅŸturulur)
    sendOrderEmails(order, orderNumber).catch(error => {
      console.error('âŒ Error sending order emails:', error);
      // Mail gÃ¶nderme hatasÄ± sipariÅŸ oluÅŸturmayÄ± engellemez
    });
    
    return NextResponse.json({
      success: true,
      id: docRef.id,
      orderNumber,
      order: { id: docRef.id, ...order },
      message: 'Order created successfully in Firebase',
      timestamp: new Date().toISOString()
    });
    
  } catch (error) {
    console.error('âŒ Error creating order:', error);
    console.error('âŒ Error stack:', error instanceof Error ? error.stack : 'No stack trace');
    
    if (error instanceof Error && error.message.includes('permission')) {
      return NextResponse.json({
        error: 'Firebase permission denied',
        details: 'Check Firebase security rules',
        message: error.message,
        timestamp: new Date().toISOString()
      }, { status: 403 });
    }
    
    return NextResponse.json({
      error: 'Failed to create order',
      details: error instanceof Error ? error.message : 'Unknown error',
      timestamp: new Date().toISOString()
    }, { status: 500 });
  }
}

export async function GET() {
  try {
    console.log('ðŸ“¦ Fetching orders...');
    
    const ordersQuery = query(
      collection(db, 'orders'),
      orderBy('createdAt', 'desc'),
      limit(100)
    );
    
    const querySnapshot = await getDocs(ordersQuery);
    const orders = querySnapshot.docs.map(doc => ({
      id: doc.id,
      ...doc.data()
    }));
    
    console.log(`âœ… Found ${orders.length} orders`);
    
    return NextResponse.json({
      success: true,
      orders,
      count: orders.length,
      timestamp: new Date().toISOString()
    });
    
  } catch (error) {
    console.error('âŒ Error fetching orders:', error);
    
    return NextResponse.json({
      error: 'Failed to fetch orders',
      details: error instanceof Error ? error.message : 'Unknown error',
      timestamp: new Date().toISOString()
    }, { status: 500 });
  }
}